:root {
    --borderColor: #ff991a;
}
.home-intro {
    position: relative;
    cursor: pointer;
}
.home-intro::before, .home-intro::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    transition: 0.3s ease-in-out;
    
}
.home-intro::before {
    top: -5px;
    left: -5px;
    border-top: 3px solid var(--borderColor);
    border-left: 3px solid var(--borderColor);
}
.home-intro::after {
    right: -5px;
    bottom: -5px;
    border-bottom: 3px solid var(--borderColor);
    border-right: 3px solid var(--borderColor);
}
.home-intro:hover::before, .home-intro:hover::after {
    width: calc(100% + 9px);
    height: calc(100% + 9px);
}
